import UserModel from './../models/userModel'

class UserController {
    constructor(){
        const userModel = new UserModel();
    }
    executeQuery = (req, res) =>{
        userModel.executeQuery(req).then((dbResponse)=>{
            res.status(dbResponse.satusCode).json(dbResponse);
        }).catch((err)=>{
            res.status(dbResponse.satusCode).json(err);
        })
    }

    registerUser= (req, res) =>{
        userModel.registerUser(req).then((dbResponse)=>{
            res.status(dbResponse.satusCode).json(dbResponse);
        }).catch((err)=>{
            res.status(dbResponse.satusCode).json(err);
        })
    }
    
    loginUser = (req, res) => {
        userModel.loginUser(req).then((dbResponse)=>{
            res.status(dbResponse.satusCode).json(dbResponse);
        }).catch((err)=>{
            res.status(dbResponse.satusCode).json(err);
        })
    }

    getUser = (req, res) =>{
        userModel.getUser(req).then((dbResponse)=>{
            res.status(dbResponse.satusCode).json(dbResponse);
        }).catch((err)=>{
            res.status(dbResponse.satusCode).json(err);
        })
    }
}

export default UserController;